<template>
  <label class="mo-upload">
    <input type="file" :accept="accepts" @change="upload">
    <slot></slot>
  </label>
</template>
<style >
  .mo-upload {
    display: inline-block;
    position: relative;
    margin-bottom: 0;
  }
  input[type="file"] {
    display: none;
  }
  .mo-upload--label {
    display: inline-block;
    position: relative;
  }
</style>
<script>
  import getUuid from '../../util/Uuid'
  export default {
    name : 'MoUpload',
    props : {
      accepts : { //
        type : String,
        default : 'image/jpeg,image/jpg,image/png,image/gif'
      },
      flag : [String, Number], //,
      maxSize : {
        type : Number,
        default : 0 //
      },
    },
    methods: {
      upload (event) {
        let file = event.target.files[0];
        const self = this;
        const flag = this.flag;
        if (file) {
          if (this.maxSize) {
            //todo filter file
          }
          //filter file, ,
          //
          // const reader = new FileReader()
          // const imageUrl = reader.readAsDataURL(file)
          // img.src = imageUrl //

          const formData = new FormData();
          formData.append('file', file);

          //token
          this.$http.get(`http://localhost:8087/uptoken`)
            .then(response => {
              const result = response.body;
              formData.append('token', result.uptoken);
              // 
              formData.append('key', getUuid(13, 16));
              //
              self.$http.post('https://upload-z2.qbox.me', formData, {
                progress(event) {
                  //progress
                  self.$emit('progress', parseFloat(event.loaded / event.total * 100), flag)
                }
              })
                .then(response => {
                  const result = response.body;
                  if (result.hash && result.key) {
                    //complete
                    self.$emit('complete', 200 , result, flag);
                    //target
                    event.target.value = null
                  } else {
                    self.$emit('complete', 500, result, flag)
                  }
                }, error => self.$emit('complete', 500, error.message), flag)
            })
        }
      }
    }
  }
</script>



